<template>
  <v-container
    fluid
  >
    <v-row>
      <template>
        <v-col
          cols="12"
          lg="3"
          sm="6"
        >
          <base-material-stats-card
            color="info"
            icon="mdi-bulletin-board"
            sub-icon="mdi-clock"
            :title="$t('今日营业总额')"
            :value="'€'+todayInfo.totalIncome"
          />
        </v-col>

        <v-col
          cols="12"
          lg="3"
          sm="6"
        >
          <base-material-stats-card
            color="primary"
            icon="mdi-poll"
            sub-icon="mdi-tag"
            :title="$t('总单数')"
            :value="todayInfo.totalCount"
          />
        </v-col>

        <v-col
          cols="12"
          lg="3"
          sm="6"
        >
          <base-material-stats-card
            color="success"
            icon="mdi-store"
            sub-icon="mdi-calendar"
            sub-text=""
            :title="$t('酒水营业额')"
            :value="'€'+todayInfo.drinkInfo"
          />
        </v-col>

        <v-col
          cols="12"
          lg="3"
          sm="6"
        >
          <base-material-stats-card
            color="orange"
            icon="mdi-sofa"
            sub-icon="mdi-alert"
            :title="$t('食品营业额')"
            :value="'€'+todayInfo.foodInfo"
          />
        </v-col>
      </template>
      <v-col
        cols="12"
        md="4"
      >
        <base-material-card
          class="px-5 py-3 "
          color="indigo"
          icon="mdi-vuetify"
          inline
        >
          <template v-slot:after-heading>
            <div class="display-2 font-weight-light">
              {{ $t('服务员营业额') }}
            </div>
          </template>
          <v-divider class="mt-3"/>
          <v-data-table
            :fixed-header="true"
            :headers="staffHeaders"
            :items="staffArr"
            :items-per-page="10"
            :loading="loading"
            :search.sync="search"
            multi-sort
          >
            <template v-slot:item.paymentStatus="{ item }">
              <v-checkbox
                :readonly="true"
                :input-value="item.paymentStatus==='1'"
              />
            </template>
          </v-data-table>
        </base-material-card>
      </v-col>
      <v-col
        cols="12"
        md="8"
      >
        <v-container
          id="data-tables"
          class="fill-height align-start"
          fluid
          tag="section"
        >
          <v-container
            fluid
          >
            <base-material-card
              class="px-5 py-3 "
              color="indigo"
              icon="mdi-calendar-account"
              inline
            >
              <template v-slot:after-heading>
                <div class="display-2 font-weight-light">
                  {{ $t('订单统计') }}
                </div>
              </template>
              <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                class="ml-auto"
                hide-details
                :label="$t('Search')"
                single-line
                style="max-width: 250px;"
              />
              <v-divider class="mt-3"/>
              <v-data-table
                :fixed-header="true"
                :headers="headers"
                :items="desserts"
                :items-per-page="10"
                :loading="loading"
                :search.sync="search"
                multi-sort
              >
                <template v-slot:item.paymentStatus="{ item }">
                  <v-checkbox
                    :readonly="true"
                    :input-value="item.paymentStatus==='1'"
                    disabled
                  />
                </template>
                <template v-slot:item.action="{ item }">
                  <v-icon
                    class="mr-2"
                    small
                    @click="goNext(item)"
                  >
                    mdi-arrow-right
                  </v-icon>
                </template>
                <template v-slot:no-data>
                  <v-btn
                    color="primary"
                    @click="loadDetail"
                  >
                    Reset
                  </v-btn>
                </template>
              </v-data-table>
            </base-material-card>
          </v-container>
        </v-container>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>

  import { getTodayBills } from '../../components/Utlis/api'
  import Utils from '../../components/Utlis/Utils'

  export default {
    data: function () {
      return ({
        loading: true,
        dialog: false,
        infoList: [],
        BillData: {},
        headers: [
          {
            text: 'Id',
            align: 'start',
            sortable: false,
            value: 'orderId',
          },
          {
            text: this.$i18n.t('servantName'),
            value: 'servantName',
          },
          {
            text: this.$i18n.t('time'),
            value: 'eatTime',
          },
          {
            text: this.$i18n.t('price'),
            value: 'totalPrice',
          },
          {
            text: this.$i18n.t('paymentStatus'),
            value: 'paymentStatus',
          },
          {
            text: this.$i18n.t('action'),
            value: 'action',
            sortable: false,
          },
        ],
        search: '',
        desserts: [],
        staffHeaders: [
          {
            text: this.$i18n.t('servantName'),
            value: 'name',
          },
          {
            text: this.$i18n.t('income'),
            value: 'income',
          },
          {
            text: this.$i18n.t('billCount'),
            value: 'billCount',
          },
        ],
        staffArr: [],
        editedIndex: -1,
        editedItem: {
          foodInfo: '',
          drinkInfo: '',
          discount: '',
          totalCount: '',
          totalPrice: '',
          totalIncome: '',
        },
        defaultItem: {
          foodInfo: '',
          drinkInfo: '',
          discount: '',
          totalCount: '',
          totalPrice: '',
          totalIncome: '',
        },
      })
    },

    computed: {
      formTitle () {
        return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
      },
      todayInfo () {
        console.log('it is:' + this.BillData)
        return this.BillData
      },
    },

    watch: {
      dialog (val) {
        val || this.close()
      },
    },

    created () {
      this.loadDetail()
    },

    methods: {
      loadDetail () {
        getTodayBills().then(res => {
          this.BillData = res
          this.infoList = []
          this.infoList.push({ text: '总营业额：' + this.BillData.totalIncome })
          this.infoList.push({ text: '总单数：' + this.BillData.totalCount })
          this.infoList.push({ text: '酒水营业额：' + this.BillData.drinkInfo })
          this.infoList.push({ text: '食品营业额：' + this.BillData.foodInfo })
          this.desserts = this.BillData.todayBill
          this.staffArr = this.BillData.servantRank
          this.loading = false
        })
      },
      goNext (item) {
        this.$router.push({
          name: 'Seite mit den Rechnungsdetails',
          params: { id: item.orderId },
        })
      },
      editItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
      },

      deleteItem (item) {
        const index = this.desserts.indexOf(item)
        Utils.showConfirm('Are you sure?', 'you want to delete this item?', () => {
          this.desserts.splice(index, 1)
        })
      },

      close () {
        this.dialog = false
        setTimeout(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        }, 300)
      },

      save () {
        if (this.editedIndex > -1) {
          Object.assign(this.desserts[this.editedIndex], this.editedItem)
        } else {
          this.desserts.push(this.editedItem)
        }
        this.close()
      },
    },
  }
</script>
